import string from "./string.js";
/*Â¶Ç‰Ωï‰øùÁïôÁ©∫Ê†ºÔºüÔºüÔºü*/
let n = 1;
let substr = "";
substr = string.slice(0, n);
demo.innerHTML = substr;
style.innerHTML = substr;
// ÂÆöÊó∂Âô®ÂáΩÊï∞
let fn = () => {
  console.log(n,string.length)
  if(n === string.length){
    clearInterval(id);
    return ;
  }
  n<string.length && (n=n+1);
  // ÊØèÊ¨°‰ªéstring‰∏≠ËøΩÂä†‰∏Ä‰∏™Â≠óÁ¨¶Âà∞substrÈáåÔºåÂ¶ÇÊûúÈÅáÂà∞Á©∫Ê†ºÂ∞±ËøΩÂä†ËΩ¨‰πâÂ≠óÁ¨¶ÔºåÂ¶ÇÊûúÈÅáÂà∞Êç¢Ë°åÂ∞±ËøΩÂä†<br>
  if (
    string
      .slice(0, n)
      .slice(n - 1, n)
      .charCodeAt() === 32
  ) {
    // ÂΩìÂâçÊåáÂêëÁöÑstringÁöÑÊúÄÂêé‰∏Ä‰∏™Â≠óÊòØÁ©∫Ê†ºÁöÑËØù ÊàëÂ∞±ÁªôÂ≠êÂ≠óÁ¨¶‰∏≤ËΩ¨ÁßªÁ¨¶Âè∑
    substr += "&nbsp;";
  } else if (
    string
      .slice(0, n)
      .slice(n - 1, n)
      .charCodeAt() === 10
  ) {
    substr += "<br>";
  } else {
    substr += string.slice(0, n).slice(n - 1, n);
  }
  demo.innerHTML = substr;
  style.innerHTML = string.slice(0, n); // styleÈáåÊó†ÊâÄË∞ìÔºåÁõ¥Êé•ÊâîËøáÂéªÂç≥ÂèØ
  demo.scrollTop = 9999;
  // ÊñáÊú¨‰∏≠ÁöÑÁ©∫Ê†ºÂíåËΩ¨‰πâÁ¨¶Âè∑ÈÉΩ‰ºöË¢´ÊµèËßàÂô®ËØªÂà∞Ôºå‰ΩÜÊòØÂÖ∑‰ΩìÂ¶Ç‰ΩïÂ±ïÁ§∫ÁöÑÔºåËøôÊòØÂê¨ÊµèËßàÂô®ÁöÑÔºå
  // ÊµèËßàÂô®‰ºöÊäähtmlÈ°µÈù¢‰∏≠Â§ö‰∏™ÊâãÊâìÁöÑÁ©∫Ê†ºÈÉΩÁªôÊï¥ÁêÜÊàê1‰∏™

  // ÊµèËßàÂô®ËØªÂà∞‰Ω†Ëøô‰∏™ÊòØinnerTextÂ≠óÁ¨¶‰∏≤Êó∂ÔºåÂ¶ÇÊûúÂ≠óÁ¨¶‰∏≤ÈáåÊúâtag
  // ‰∏ç‰ºöÂ∏Æ‰Ω†ÊäätagËß£ÊûêÂá∫Êù•Ôºå‰ºöÊääÂ§ö‰∏™ÊâãÊâìÁöÑÁ©∫Ê†ºÈÉΩÁªôÊï¥ÁêÜÊàê1‰∏™
  // ‰ΩÜÊòØÊµèËßàÂô®‰ºöÂ∏Æ‰Ω†‰øùÁïôinnerTextÂ≠óÁ¨¶‰∏≤‰∏≠ÁöÑÊç¢Ë°åÔºå‰Ω†ÊúâÂ§ö‰∏™Êç¢Ë°åÂÆÉÂ∞±Â±ïÁ§∫Â§ö‰∏™Êç¢Ë°åÔºå

  // ÊµèËßàÂô®ËØªÂà∞‰Ω†Ëøô‰∏™ÊòØinnerHTMLÂ≠óÁ¨¶‰∏≤Êó∂Ôºå‰ºöÂ∏Æ‰Ω†ÊäätagËß£ÊûêÂá∫Êù•Ôºå‰πü‰ºöÊääËΩ¨‰πâÂ≠óÁ¨¶Ê≠£Á°ÆÊâìÂç∞Ôºå
  // ÊúâÂ§öÂ∞ë‰∏™&nbsp;Â∞±‰ºöÁîüÊïàÂ§öÂ∞ë‰∏™Á©∫Ê†ºÔºå‰ΩÜ‰æùÊóß‰ºöÊääÂ§ö‰∏™ÊâãÊâìÁöÑÁ©∫Ê†ºÈÉΩÁªôÊï¥ÁêÜÊàê1‰∏™
  // Â¶ÇÊûúinnerHTML‰∏≠Âá∫Áé∞‰∏Ä‰∏™Êç¢Ë°åÁ¨¶Âè∑ÔºåÂàôÂ±ïÁ§∫‰∏∫‰∏Ä‰∏™Á©∫Ê†ºÔºå
  // Â¶ÇÊûúinnerHTML‰∏≠Âá∫Áé∞Â§ö‰∏™Êç¢Ë°åÁ¨¶Âè∑ÔºåÂàôÂ±ïÁ§∫‰∏∫‰∏Ä‰∏™Á©∫Ê†º
};

let v = 100; // ËÆæÁΩÆÈÄüÁéá
const play = () => setInterval(fn, v);
const stop = () => {
  clearInterval(id); // ‰ªÖ‰ªÖÊòØÊääÂÆöÊó∂Âô®ÁöÑÂäüËÉΩÂÅúÊ≠¢‰∫ÜÔºå‰ΩÜ‰∏ç‰ºöÂ∞ÜidÁΩÆÁ©∫
  id=null; // Â∞ÜidÁΩÆÁ©∫ÊòØ‰∏∫‰∫ÜÂ∏ÉÂ∞îÂà§Êñ≠
}

let id = play(); // ‰∏ÄËøõÈ°µÈù¢ËøêË°åÂÆöÊó∂Âô®

// Âè™Ë¶Å‰ª£Á†ÅÈáçÂ§ç‰∫ÜÂ∞±‰∏ëÔºå‚Äò‰ºòÂåñ‚Äô‰∏Ä‰∏ã‰ª£Á†ÅÔºåÂÖ∂ÂÆûÂ∞±ÊòØËÆ©‰ªñ‰ª¨ÁúãÁùÄÂ•ΩÁúã‰∫õ
btnPause.onclick = () => {
  stop();
  console.log(id);
};
btnPlay.onclick = () => {
  console.log(id);
  if(!id){id = play();}
};
btnSlow.onclick = () => {
  v = 100;
  stop();
  id = play();
};
btnNormal.onclick = () => {
  v = 30;
  stop();
  id = play();
};
